export abstract class AbstractLink implements HyperLinkInterface {
    // 是否静谧触发，即多个时不出现下拉框
    public silent = false;
    protected abstract _excute(): void;
    protected name: string;
    public constructor(props: HyperLinkProps, ..._external: any[]) {
        const { name } = props;
        this.name = name;

        this.init(props, _external);
    }

    public init(_props: HyperLinkProps, ..._external: any[]) {}

    public excute() {
        this._excute();
    }

    public getName(): string {
        return this.name;
    }
}


export interface HyperLinkInterface {
    excute: Function;
    getName: Function;
}

export interface HyperLinkProps {
    name: string;
    width?: number;
    height?: number;
    attr?: HyperLinkAttr;
    showParameterInterface?: boolean;
}

export interface HyperLinkAttr {
    center: boolean;
    left?: number;
    top?: number;
    title?: string;
}

export interface HyperLinkParameter {
    name: string;
    id: string;
    value: number | string | boolean;
    nameSpace: string | undefined;
    data: string | undefined;
    classInfo: string | undefined;
}

export type HTMLLinkTarget = '_self' | '_blank' | '_dialog';
